.guarantee-title-text-orange {
  color: $orange-text-color;
}

@media (min-width: 768px) {
  .guarantee-list {
    background-color: #f1f1f1;
    padding-top: 30px;
    padding-bottom: 30px;
    padding-right: 50px;
    padding-left: 80px;

    & .dashed::after {
      position: absolute;
      content: '';
      display: block;
      border: 1px dashed $grey-text-color;
      height: 78%;
      top: 61%;
      left: 8px;
    }
  }

  .guarantee-list-item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  .guarantee-bg {
    background-color: transparent;
    padding-top: 0;
    padding-bottom: 0;
  }

  .guarantee-list-item-logo-svg {
    position: relative;
    z-index: 2;
    top: 0px;
    width: 27px;
    height: 18px;
  }

  //   .dashed::after {
  //     position: absolute;
  //     content: '';
  //     display: block;
  //     border: 1px dashed #b9b9b9;
  //     height: 35px;
  //     top: 55%;
  //     left: 9px;
  //   }
}

@media (min-width: 1356px) {
  .guarantee {
    padding-top: 65px;
    padding-bottom: 65px;
  }

  .guarantee-title {
    font-size: 46px;

    margin-bottom: 50px;
  }

  .guarantee-list {
    padding-top: 50px;
    padding-bottom: 45px;
    padding-right: 112px;
    padding-left: 112px;

    & .dashed::after {
      position: absolute;
      content: '';
      display: block;
      border: 1px dashed $grey-text-color;
      height: 64%;
      top: 65%;
      left: 1%;
    }
  }

  .guarantee-list-item-logo-svg {
    top: 0px;
    width: 30px;
    height: 30px;
    margin-right: 30px;
  }

  //   .dashed::after {
  //     height: 38px;
  //     top: 61%;
  //     left: 12px;
  //   }

  .guarantee-title-text {
    font-size: 20px;
    line-height: 1.4;
  }
}
