.reviews {
  padding-top: 25px;
  padding-bottom: 25px;
}

.reviews-title {
  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;

  text-align: center;
  letter-spacing: 0.03em;

  color: $black-text-color;

  margin-bottom: 40px;
}

.reviews-title-list {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 30px;
  padding-right: 20px;
  padding-left: 20px;
}

.reviews-img {
  margin: 0 auto;
  margin-bottom: 27px;
}

.reviews-list-item-fb-svg {
  margin-bottom: 20px;
  fill: #3f51b5;
  width: 30px;
  height: 30px;
}

.reviews-title-name {
  font-weight: 800;
  font-size: 14px;
  line-height: 1.21;
  white-space: pre-line;
  letter-spacing: 0.08em;

  color: $orange-text-color;

  margin-bottom: 10px;
}

.reviews-title-name-text-education {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.08em;
  white-space: pre-line;

  color: $grey-text-color;
  margin-bottom: 10px;
}

.reviews-title-name-text-comment {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;

  color: $secondary-black-text-color;
}

.reviews-title-name-text-link {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;

  text-decoration-line: underline;

  color: $accent-color;
}

.reviews-graduates-slider {
  margin-top: 30px;
  position: relative;
  display: flex;
  justify-content: center;
}

.reviews-graduates-list {
  &:not(:last-child) {
    margin-right: 10px;
  }
}
.reviews-title-list-item {
  width: 90px;
  margin: 0 auto;
}
.arrow-left{
  position: absolute;
  bottom: -20px;
  left: 90px;
  width: 27px;
  height: 16px;
  background-image: url('./../images/svg/Arrow-1.svg');
  &:hover{
    background-color: white;
  }
}
.arrow-right{
  position: absolute;
  bottom: -20px;
    right: 90px;
  width: 27px;
  height: 16px;
  background-image: url('./../images/svg/Arrow-2.svg');
  &:hover{
    background-color: white;
  }
}
.reviews-box-shadow{
  background-color: $white-background-color;
  box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
}
@media screen and (max-width: 767px) {
  .reviews-graduates-list-t-d {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .reviews-title {
    font-size: 26px;
    line-height: 1.3;
    margin-bottom: 30px;
  }

  .reviews-title-list {
    display: flex;

    padding-top: 40px;
    padding-bottom: 40px;
    padding-right: 40px;
    padding-left: 40px;
  }

  .reviews-title-list-item {
    margin-right: 46px;
  }
  .reviews-list-item-fb-svg {
    margin-bottom: 0;
  }

  .reviews-title-list-item-tablet {
    text-align: start;
    flex: 56;
  }

  .reviews-title-name {
    font-size: 20px;
    line-height: 1.2;

    margin-bottom: 20px;
  }
  .reviews-title-name-text-education {
    margin-bottom: 20px;
  }

  .reviews-title-name-text-comment {
    padding-left: 0;
    padding-right: 0;
    white-space: unset;
  }
  .arrow-left{
    position: absolute;
    top: 155px;
    left: -30px;
    width: 27px;
    height: 16px;
    background-image: url('./../images/svg/Arrow-1.svg');
    &:hover{
      background-color: white;
    }
  }
  .arrow-right{
    position: absolute;
    top: 155px;
    right: -30px;
    width: 27px;
    height: 16px;
    background-image: url('./../images/svg/Arrow-2.svg');
    &:hover{
      background-color: white;
    }
  }
}

@media screen and (min-width: 1356px) {
  .reviews {
    padding-top: 65px;
    padding-bottom: 65px;
  }

  .reviews-title {
    font-size: 46px;
    margin-bottom: 50px;
  }

  .reviews-title-list {
    padding-top: 50px;
    padding-bottom: 51px;
    padding-left: 82px;
    padding-right: 82px;

    width: 1090px;
    margin: 0 auto;
    margin-bottom: 40px;
  }

  .reviews-title-list-item {
    text-align: center;
    margin-right: 40px;
  }

  .reviews-title-name {
    font-size: 30px;
    margin-bottom: 10px;
  }

  .reviews-title-name-text-education {
    font-size: 14px;
    line-height: 1.21;
    margin-bottom: 20px;
    width: 447px;
    white-space: normal;
  }

  .reviews-title-name-text-comment {
    font-size: 18px;
    line-height: 1.56;
  }

  .reviews-title-name-text-link {
    font-size: 18px;
    line-height: 1.56;
  }

  .reviews-graduates-list {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
}
.btn-caorusel {
  background-color: transparent;
  border: 0;
  outline: 0;
  border-radius: 50%;
  cursor: pointer;
}
